import React, { useState, useEffect } from "react";
import Todo from "./Todo";
import db from "./Firebase";
import firebase from "firebase";

function App() {
     const [Todos, setTodos] = useState([]);
     const [Input, setInput] = useState("");

     useEffect(() => {
          db.collection("firebaseDatas")
               .orderBy("timestamp", "desc")
               .onSnapshot((snapshot) => {
                    setTodos(snapshot.docs.map((doc) => doc.data().firebaseData));
               });
     }, []);

     const addTodo = (event) => {
          db.collection("firebaseDatas").add({
               firebaseData: Input,
               timestamp: firebase.firestore.FieldValue.serverTimestamp(),
          });
          setInput("");
     };
     return (
          <div className="flex items-center justify-center flex-col mt-14">
               <p className="text-5xl">To-do List üìï</p>
               <div className="flex items-center justify-center flex-col mt-14">
                    <input type="text" className="rounded-lg text-red-800 p-1 border-2 border-red-800 font-bold" value={Input} onChange={(event) => setInput(event.target.value)} />
                    <button disabled={!Input} className="bg-red-600 p-1 rounded-xl mt-3" onClick={addTodo}>
                         Add Todo
                    </button>
                    <div className="flex flex-col items-center justify-center space-y-2">
                         {Todos.map((todo) => (
                              <Todo text={todo} />
                         ))}
                    </div>
               </div>
          </div>
     );
}

export default App;
